import React from "react";

interface ButtonComponentProps {

  label: string,
  type: 'button' | 'submit',
  onClick?: () => void,

}

const ButtonComponent: React.FC<ButtonComponentProps> = (props) => {

  const { label, type = 'button', onClick } = props;

  const handleTest = () => {

    onClick();

  }

  return (

    <button type={type} onClick={handleTest}>{label ? label : "bouton"}</button>

  );

};

export default ButtonComponent;
